import { Component, OnDestroy, OnInit } from "@angular/core";
import { MatBottomSheet } from "@angular/material/bottom-sheet";
import { Subscription } from "rxjs";
import { tap } from "rxjs/operators";
import { BottomSheetDataComponent } from "src/app/bottom-sheet-data/bottom-sheet-data.component";
import { ApiService } from "src/app/services/api.service";

@Component({
  selector: "app-support",
  templateUrl: "./support.component.html",
  styleUrls: ["./support.component.scss"],
})
export class SupportComponent implements OnInit, OnDestroy {
  private subs: Subscription[] = [];
  buttonDisabled: boolean = false;

  constructor(
    private service: ApiService,
    private bottomSheet: MatBottomSheet
  ) {}

  enableRemoteSupport() {
    this.buttonDisabled = true;
    this.service.doRemote$().subscribe(
      (result) => {
        let msg = "";
        if (result) {
          msg = `We enabled remote support.`;
        } else {
          msg = `Seems like it didn't work. Please cycle power on your device, check your internet connection and try again.`;
        }
        this.bottomSheet.open(BottomSheetDataComponent, {
          data: { message: msg, progress: result },
        });
        setTimeout(() => {
          this.bottomSheet.dismiss();
        }, 1600);
      },
      (error) => {
        console.log(error);
      }
    );
  }

  disableRemoteSupport() {
    this.service.killRemote$().subscribe(
      (result) => {
        this.buttonDisabled = false;
        const msg = `We turned off remote support.`;
        this.bottomSheet.open(BottomSheetDataComponent, {
          data: { message: msg, progress: result },
        });
        setTimeout(() => {
          this.bottomSheet.dismiss();
        }, 1600);
      },
      (error) => {
        console.log(error);
      }
    );
  }

  ngOnInit(): void {
    var sub = this.service
      .getRemotePID$()
      .pipe(
        tap((data) => {
          this.buttonDisabled = !!data;
        })
      )
      .subscribe();
    this.subs.push(sub);
  }

  ngOnDestroy() {
    this.subs.forEach((sub) => {
      sub.unsubscribe();
    });
  }
}
