export class Output {
  public output = "";
  public dop = false;
  public volume = false;
  public bufferDuration = 0.04;
  public maxBits = 32;

  constructor(jsonData?: Output) {
    Object.assign(this, jsonData);
    if (typeof jsonData.maxBits === "string") {
      this.maxBits = parseInt(jsonData.maxBits);
    }
  }
}
